import React, { Component } from 'react';
//import logo from './logo.svg';
//import './App.css';
import base from '../base';
import Tweet from './Tweet';

import sampleTweets from '../sample-tweets';

class App extends Component {
  constructor() {
    super();

  //this.addFish = this.addFish.bind(this);
  this.loadTweets = this.loadTweets.bind(this);
    //getinitialState
    this.state = {
      tweets: {},
      favorites: {}
    };
  }

  componentWillMount(){
    //this runs right before the app is rendered
    this.ref = base.syncState(`hackmods`,
    {
      context: this,
      state: 'tweets'
    });
  }

  componentWillUnmount(){
    base.removeBindings(this.ref);
  }

  loadTweets() {
    this.setState({
      tweets: sampleTweets
    });
  }

  render() {
    return (
      <div className="App">
        <div className="card-deck">
            {
              Object
                .keys(this.state.tweets)
                .map(key => <Tweet key={key} index={key} details={this.state.tweets[key]}/>)
                //use map to go through object collection fishes
                //create a unique key per fish
                //use said key as a unique key to identify each fish, we reuse the key from the loop because its already unique
                //specify details for each object by pointing to the unique fish in fishes via your key index
                //yea its a lot to digest....
 
                //object.keys allow us to go through our collection
            }
          </div>
      </div>
    );
  }
}

export default App;



